/* /Barra de navegacion de la pagina */
* {
	text-decoration: none;
	list-style: none;
	overflow: hidden;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.btn-primary {
	background-color: #7e2ae6 !important;
	position: relative;
	top: 1.5em;
	width: 34vw;
}

.nav {
	position: fixed;
	background-color: black;
	display: flex;
	align-items: baseline;
	width: 100vw;
	height: 10vh;
	left: 0px;
	top: 0px;
	align-content: center;
	z-index: 6;
}

.logo {
	padding-left: 3em;
	color: white;
	height: 38px;
	display: flex;
}

.nav>a {
	text-decoration: none;
}

.img {
	position: absolute;
	display: inline-flex;
	align-items: baseline;
	width: 15px;
	height: 38px;
	background: linear-gradient(180deg, #5F40DE 0%, rgba(95, 64, 222, 0) 100%);
	box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.get {
	margin-left: 20px;
}

.lista {
	left: 60%;
	margin-top: 1em;
	display: inline-flex;
	position: relative;
	color: white;
	justify-content: space-evenly;
	width: 25vw;
}

.signup {
	background-color: #5F40DE;
	border-radius: 15px;
	padding: 0.2em 0.2em 0.4em 0.4em;
}

.signup>a {
	color: white;
	text-decoration: none;
}

.vector {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 40%;
	z-index: -1;
}

.container_title {
	position: relative;
	top: 2em;
	left: 0.6em;
	font-size: 4rem;
	width: 60vw;
	height: 60vh;
	color: black;
}

.derechos {
	position: absolute;
	bottom: 0.8em;
	right: 0.8em;
	z-index: 3;
	color: white;
	width: 20vw;
}


.form {
	position: relative;
	top: 2em;
	height: 30em;
	display: flex;
	justify-content: center;
	flex-direction: column;
	justify-self: center;
	z-index: 12;
}

.form-login {
	position: relative;
	min-height: 30vh;
	height: auto;
	display: flex;
	flex-direction: column;
	z-index: 3;
}

.titulo_registro,
.form-group {
	display: flex;
	justify-content: center;
	margin-bottom: 2vh;
}

input {
	background-color: transparent;
	padding: 0.7em;
	height: 6vh;
	width: 50vw;
	border: 1px solid #BBB5B5;
	box-sizing: border-box;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	border-radius: 10px;
}

.blue-input:focus {
	background-color: transparent;

}

.input-login {
	height: 6vh;
	width: 31vw;
	border: 1px solid #BBB5B5;
	box-sizing: border-box;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	border-radius: 10px;
}

button {
	background: #000000;
	border-radius: 15px;
	height: 6vh;
	width: 40vw;
	color: white;
}

.prompt {
	color: rgb(255, 0, 0);
	display: none;
}

.modal-body {
	height: 50vh;
	display: flex;
	justify-content: center;
}

.btn-register {
	border: none;
	border-radius: 10px;
}

@media (max-width: 800px) {
	.nav>ul {
		position: relative;
		left: 14em;
		width: 40vw;
		align-items: baseline;
		display: flex;
	}

	.signup {
		width: 35vw;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 5vh;
	}

	.login {
		width: 35vw;
	}
}